import { Component } from "@angular/core";
import gsap from "gsap";
declare var $: any;

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"]
})
export class AppComponent {
  constructor() {}
  ngAfterViewInit(): void {
    setTimeout(() => {
      $(".slider").slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        mobileFirst: true,
        autoplaySpeed: 2000,
        dots: true,
        arrows: true
      });

      const imageContainer = $(".image-slides");
      const description = $(".description");
      const descriptionHeader = description.find("h1");
      const descriptionQuote = description.find("p");
      const descriptionIcons = description
        .find(".qr-section")
        .find(".phone,.qr-code");
      const specs = $(".specs").find(".spec");
      const descriptionLink = description.find(".qr-link");
      const mainInfos = $(".main-infos");
      const mainInfosContent = mainInfos.find(".location, .price ,.address");
      const sliders = $(".slider-paths .path");
      const slideLeft = {
        delay: 0.5,
        duration: 0.3,
        force3D: true,
        x: "100%"
      };
      const slideUp = {
        delay: 0.5,
        duration: 0.3,
        force3D: true,
        y: "100%"
      };
      function setup() {
        gsap.set([descriptionHeader, descriptionQuote], {
          transformOrigin: "0 100%",
          scaleY: 0
        });
        gsap.set(mainInfosContent, {
          transformOrigin: "0 50%",
          scale: 0
        });
        gsap.set(descriptionIcons, {
          scale: 0
        });
        gsap.set(descriptionLink, {
          scale: 0
        });
        gsap.to(sliders, {
          duration: 3,
          stagger: 0.1
        });
      }
      function animateBasic(config, callback) {
        gsap.from(imageContainer, config);
        gsap.from(description, {
          ...config,
          delay: config.delay + 0.1
        });
        gsap.from(mainInfos, {
          ...config,
          delay: config.delay + 0.1,
          onComplete: callback
        });
      }
      function animateContent() {
        const tl = gsap.timeline();

        tl.to([descriptionHeader, descriptionQuote], {
          duration: 0.5,
          force3D: true,
          scaleY: 1
        })
          .to(mainInfosContent, {
            transformOrigin: "0 50%",
            force3D: true,
            duration: 0.3,
            scale: 1
          })
          .to(descriptionIcons, {
            force3D: true,
            stagger: 0.2,
            duration: 0.5,
            scale: 1
          })
          .to(descriptionLink, {
            transformOrigin: "0 50%",
            force3D: true,
            duration: 0.3,
            scale: 1
          });
        tl.play();
      }
      //
      setup();
      animateBasic(slideUp, animateContent);
    }, 1000);
  }
}
